<template>
  <div class="setEdit">
      <div class="setTop">
        <span>当前位置:
          <i>设置</i>
        </span>
      </div>
    <div class="setContent clearfix">
      <div class="setLeft">
        <div>
          <div class="kuang">
            <h1>个人信息</h1>
          </div>
          <div class="kuang">
            姓名:
            <!--<input type="text" v-model="perInfor.name" class="inputs">-->
            <el-input v-model="perInfor.name" class="inputs" @blur="showErr1"></el-input>
            <div v-if="perInfor.name== ''" v-show="isErr1" class="isErr">姓名不能为空</div><br>
          </div>
          <div class="kuang">
            所属地区:
            <!--<input type="text" v-model="perInfor.section_one" class="inputs">-->
            <el-select v-model="perInfor.section_one" placeholder="地区" class="inputs" @change="getRegDep(perInfor.section_one)">
              <el-option :label="item" :value="item" v-for="(item,keys) in regions" :key="item"></el-option>
            </el-select>
          </div>
          <div class="kuang">
            所属部门:
            <!--<input type="text" v-model="perInfor.section_two" class="inputs">-->
            <el-select v-model="perInfor.section_two" placeholder="部门" class="inputs" @blur="showErr3">
              <el-option  v-for="(item,keys) in departs"
                :label="item.section_two"
                :value="item.id"
                :key="item.id">
              </el-option>
            </el-select>
            <div v-if="perInfor.section_two== ''" v-show="isErr3" class="isErr">部门不能为空</div><br>
          </div>
        </div>

        <div>
          <div class="kuang">
            <h2>修改密码</h2>
          </div>
          <div class="kuang">
              重置密码:
            <!--<input type="text" v-model="password" class="inputs">-->
            <el-input v-model="password" class="inputs" @blur="showErr2" type="password"></el-input>
            <div v-if="password== ''" v-show="isErr2" class="isErr">密码不能为空</div><br>
          </div>
        </div>

        <button @click="editInfor()">保存修改</button>
      </div>
      <div class="setRight">
        <div class="topImg">
          <!--<el-upload-->
            <!--class="avatar-uploader"-->
            <!--action="https://jsonplaceholder.typicode.com/posts/"-->
            <!--:show-file-list="false"-->
            <!--:on-success="handleAvatarSuccess"-->
            <!--:before-upload="beforeAvatarUpload">-->
            <!--<img v-if="imageUrl" :src="imageUrl" class="avatar">-->
            <!--<i v-else class="el-icon-plus avatar-uploader-icon"></i>-->
          <!--</el-upload>-->
            <img src="../../assets/avatar.jpg" alt="">
          <p>{{perInfor.name}}</p>
        </div>
        <!--<div class="submit">-->
          <!--<button @click="handleAvatarSuccess">上传头像</button>-->
        <!--</div>-->
      </div>
    </div>
  </div>

</template>
<script>
    export default {
        data(){
            return{
                imageUrl: '',
                perInfor:{
                    section_id:'',
                    name:'',
                    section_one:'',
                    section_two:'',
                },//人员基本信息
                regions:'',
                departs:[],
                password:'',//重置密码
                regn:'',//初始地区
                dept:'',//初始部门
                isErr1: false,
                isErr2: false,
                isErr3: false,
            }
        },
        created(){
            this.$store.state.showHeader = '5';
            this.getInfor();
            this.getRegDep();
        },
        methods: {
//            获取人员基本信息
            getInfor(){
                this.$axios.post('/api/user-list',{
                    "id":this.$route.params.id
                }).then((res)=> {
//                    console.log(res)
                    this.perInfor=res.data.data.data[0];
                    this.regn=res.data.data.data[0].section_one;
                    this.dept=res.data.data.data[0].section_two;
                    this.getDept();
                }).catch((error)=> {
                    console.log(error)
                })
            },

//      编辑人员信息
            editInfor(){
                let paramy={"id":this.$route.params.id};
                if(this.perInfor.name){
                    this.$set(paramy, 'name', this.perInfor.name);
                }
                if(this.perInfor.section_two!==this.dept){//判断是否选择过地区和部门
                    this.$set(paramy, 'section_id', this.perInfor.section_two);//选择过
                }else{
                    this.$set(paramy, 'section_id', this.perInfor.section_id);//没修改
                }
                if(this.password){
                    this.$set(paramy, 'password', this.password);
                }
                this.$axios.post('/api/user-save',paramy).then((res)=> {
//                    console.log(res)
                    if(res.data.message=='成功'){
                        this.$message({
                            message: '设置成功',
                            type: 'success'
                        });
                    }
                }).catch((error)=> {
                    console.log(error)
                })
            },

//            初始化部门
            getDept(){
                this.$axios.post('/api/section-list',{
                  "section_one":this.regn
                }).then((res)=> {
//                    console.log(res)
                    this.departs=res.data.data.data;
                }).catch((error)=> {
                    console.log(error)
                })
            },

//      地区部门选择
            getRegDep(val){
                let reg=[];//地区暂存
                let dep=[];//部门暂存
                this.departs=[];
                this.$axios.post('/api/section-list',{
                    section_one:val
                }).then((res)=> {
//                    console.log(res)
                    if(!val){
                        for (var i in res.data.data.data){
                            reg.push(res.data.data.data[i].section_one);
                        }
                        this.regions=this.unique(reg);
                    }else{
                        this.perInfor.section_two='';
                        this.departs=res.data.data.data;
                    }
                }).catch((error)=> {
                    console.log(error);
                })
            },

//    去重函数
        unique(array) {
            var obj = {};
            return array.filter(function(item, index, array){
                return obj.hasOwnProperty(typeof item + JSON.stringify(item)) ? false : (obj[typeof item + JSON.stringify(item)] = true)
            })
        },

//      错误信息提示
            showErr1() {
                this.isErr1 = true;
            },
            showErr2() {
                this.isErr2 = true;
            },
            showErr3() {
                this.isErr3 = true;
            },

            handleAvatarSuccess(res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isJPG) {
                    this.$message.error('上传头像图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            }
        }
    }
</script>
<style lang="less" rel="stylesheet/less">
  @import '../../../static/css/base.css';
  .setEdit{
    margin: 0 auto;
    width: 1280px;
    .setTop{
      width: 100%;
      height: 40px;
      line-height: 40px;
      background-color: #f1f1f1;
      margin-bottom: 23px;
      span{
        margin-left: 80px;
        i{
          color:#4a90e2;
          cursor: pointer;
        }
      }
    }
    .setContent{
      // width: 100%;
      height: 100%;
      margin: 0 auto;
      background-color: #f1f1f1;
      .setLeft{
        /*margin-left: 60px;*/
        margin-left: 35px;
        margin-right: 25px;//后期添加
        padding-top: 20px;
        width: 900px;
        float: left;
        .kuang{
          margin-top: 20px;
          width: 893px;
          height: 45px;
          line-height: 45px;
          background-color:white;
          margin-bottom: 25px;
          /*text-indent:25px;*/
          padding-left: 25px;
          h1{
            line-height: 45px;
            color: #f5a623;
            /*text-indent:25px;*/
          }
          h2{
            line-height: 45px;
            color: #f5a623;
            /*text-indent:25px;*/
            margin-top:40px;
          }
          .inputs{
            outline:none;
            /*float: right;*/
            /*width: 554px;*/
            width:300px;
            height: 40px;
            position: absolute;
            right:50%;
            /*span{*/
              /*outline:none;*/
              /*float: right;*/
              /*!*width: 554px;*!*/
              /*height: 40px;*/
            /*}*/
          }
          .isErr{
            position: relative;
            left: 31.5%;
            color: red;
            top: -10px;
          }
        }
        button{
          width: 283.6px;
          height: 42px;
          border-radius: 3px;
          background-color: #4a90e2;
          cursor: pointer;
          color:white;
          margin:148px 0  36px 339px;
          outline:none;
          position: relative;
          top:-40px;
        }
      }
      .setRight{
        margin-right: 50px;
        float: right;
        .topImg{
          width: 250px;
          height: 239px;
          background-color: #ffffff;
          margin: 40px 0 21px 0;
          box-sizing:border-box;
          .avatar-uploader .el-upload {
            border: 1px dashed #d9d9d9;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
            left: 35px;
            top: 10px;

          }
          .avatar-uploader .el-upload:hover {
            border-color: #409EFF;
          }
          .avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 178px;
            height: 178px;
            line-height: 178px;
            text-align: center;
          }
          .avatar {
            width: 178px;
            height: 178px;
            display: block;
          }
          p{
            font-size: 14px;
            text-align: center;
            color: #4a4a4a;
            margin-top: 10px;
          }
        }
        .submit{
          button{
            width: 250px;
            height: 42px;
            border-radius: 3px;
            background-color: #4a90e2;
            outline:none;
            cursor: pointer;
            color:white;
          }
        }
      }
    }
    .el-input__inner{
      border: 0;
      padding: 0;
      color:#000;
      width:300px;
    }
    .el-input__icon {
      height: -1%;
      width: 39px;
    }
  }

</style>



